@import url(https://fonts.googleapis.com/css?family=Righteous|Roboto+Slab&display=swap);
@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
:root{--c-primary: #1e5c62;--c-secondary: #86e2d5;--c-accent-lime: #c9f227;--c-accent-purple: #b200fd;--text-light: #d2d7d3;--c-white: #ffffff;--c-gray-98: #f3f3f3;--c-gray-95: #ededed;--c-gray-90: #e3e3e3;--c-gray-85: #d9d9d9;--c-gray-80: #cccccc;--c-gray-75: #c0c0c0;--c-gray-70: #b0b0b0;--c-gray-60: #999999;--c-gray-50: #808080;--c-gray-45: #727272;--c-gray-40: #696969;--c-gray-30: #4d4d4d;--c-gray-20: #363636;--c-gray-15: #222222;--c-gray-10: #171717;--c-black: #000000}:root{--fontSize-sm: 0.5rem;--fontSize-normal: 1rem;--fontSize-lg: 1.5rem;--fontSize-xl: 2rem;--heading-font: "Righteous", cursive;--body-font: "Roboto Slab", serif;--hand-font: "Permanent Marker", cursive;--fontWeight-normal: 400;--fontWeight-bold: 700}:root{--mobile: 450px;--tablet: 770px;--desktop: 1024px}body{background:#f3f3f3;background:var(--c-gray-98);margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.Hero{display:flex;flex-direction:row;height:20rem;justify-content:center;margin-top:15rem;width:100%}@media (max-width: 1024px){.Hero{margin-top:13rem}}.Hero__card{flex-grow:1;max-width:50%;padding:2rem}@media (max-width: 1024px){.Hero__card{max-width:80%}}.Hero__card-text{font-family:var(--body-font);font-size:var(--fontSize-lg)}@media (max-width: 1024px){.Hero__card-text{font-family:var(--body-font);font-size:var(--fontSize-normal)}}.AboutPage{height:20rem;margin-top:15rem;width:100%}@media (max-width: 1024px){.AboutPage{margin-top:13rem}}.AboutPage__card{align-items:center;display:grid;grid:"title" "content" "redirect" / 1fr;margin:0 auto;padding:2rem;width:40rem}@media (max-width: 1024px){.AboutPage__card{max-width:80%}}.AboutPage__card-text,.AboutPage__card-title{font-family:var(--body-font);font-size:var(--fontSize-normal)}@media (max-width: 1024px){.AboutPage__card-text{font-family:var(--body-font);font-size:var(--fontSize-normal)}}.AboutPage__card-title{font-family:var(--body-font);font-size:var(--fontSize-lg)}.AboutPage__card-link{align-self:center;color:var(--c-accent-purple);grid-area:redirect;text-decoration:none;text-transform:uppercase;text-align:center}.ContactPage{display:flex;flex-direction:row;height:20rem;justify-content:center;margin-top:15rem;width:100%}@media (max-width: 1024px){.ContactPage{margin-top:13rem}}.ContactPage__card{flex-grow:1;max-width:50%;padding:2rem}@media (max-width: 1024px){.ContactPage__card{max-width:80%}}.ContactPage__card-text,.ContactPage__card-email{font-family:var(--body-font);font-size:var(--fontSize-lg)}.ContactPage__card-title{font-size:var(--fontSize-xl)}@media (max-width: 1024px){.ContactPage__card-title{font-size:calc(var(--fontSize-xl) * 0.7)}}.ContactPage__card-email{font-size:2.5rem;font-weight:var(--fontweight-bold);padding:2rem;text-align:center;text-transform:capitalize}@media (max-width: 1024px){.ContactPage__card-email{font-size:calc(var(--fontSize-lg) * 0.7);padding:2rem;text-align:center;text-transform:capitalize}}.ContactPage__card-email>a{color:var(--c-primary);text-align:center;text-decoration:none}.ContactPage__card-social{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between;width:100%}@media (max-width: 1024px){.ContactPage__card-social{margin:0 auto;width:80%}}.ContactPage__card-social>a{color:var(--c-primary);margin:0.5rem}.ResumePage{display:flex;flex-direction:row;height:20rem;justify-content:center;margin-top:15rem;width:100%}.ResumePage__card{flex-grow:1;max-width:50%;padding:2rem}.ResumePage__card-text{font-family:var(--body-font);font-size:var(--fontSize-lg);font-weight:var(--fontWeight-bold)}

.Nav-svg{fill:var(--c-accent-purple);height:calc(var(--fontSize-xl) * 1.5);width:calc(var(--fontSize-xl) * 1.5)}.Nav-svg:hover{transform:scale(1.5)}.About{transform:rotate(-25deg)}.Resume{transform:rotate(9deg)}.Contact{transform:rotate(-19deg)}.Meetup-container{align-items:center;display:flex;flex-direction:row;justify-content:center}.Meetup-container>h1{margin:0 1.25rem}@media (max-width: 1024px){.Meetup-container>h1{font-size:calc(var(--fontSize-lg) * 0.9);margin:0 1rem}}

.Header{background:var(--c-gray-98);align-items:flex-end;display:flex;flex-direction:row;height:13rem;left:0;margin-bottom:1.45rem;padding:1rem 0 2rem 1rem;position:fixed;top:0rem;width:100%}.Header>nav{display:flex;flex-direction:row;justify-content:center;margin-left:1rem}@media (max-width: 675px){.Header>nav{display:flex;flex-direction:column}}.Header__nav-link{margin:0 0.5rem;text-decoration:none}.Header__nav-link>h2{font-size:0;margin:0;padding:0;visibility:hidden}.Header__title{background:var(--c-gray-90);border-bottom:1rem solid var(--c-primary);border-radius:1rem;height:9rem;position:relative;width:15rem;transform:rotate(-15deg)}.Header__title-hello{background:var(--c-primary);border-radius:1rem 1rem 0 0;position:absolute;width:100%}.Header__title-hello>h1{color:var(--text-light);letter-spacing:1rem;margin:0;text-align:center}.Header__title-hello>p{color:var(--text-light);font-family:var(--body-font);margin:0;text-align:center}.Header__title-heading{position:relative;left:3rem;top:1rem}.Header__title-heading>a{color:var(--c-gray-10);font-family:var(--hand-font);font-size:5rem;letter-spacing:0.1rem;text-decoration:none}

